@import "./setup.css";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


#root {
    height: 100vh;
    min-height: 100vh;
    width: 100vw;

    display: flex;
    align-items: center;
    justify-content: space-around;

    background: var(--primary-color);
    overflow-x: hidden;
}

.alert {
    position: fixed;
    top: 20px;
    right: 20px;

    background-color: #fff3cd;
    color: #856404;

    padding: 12px 20px;

    border: 1px solid #ffeeba;
    border-radius: 8px;

    font-size: 1rem;
    text-align: center;
    font-weight: 500;

    z-index: 1000;

    transform: translateX(0);
    transition: transform 0.3s ease;
}

.alert.hidden {
    transform: translateX(120%);
}